export const Selectable = {
  select: (id, overwrite) => (dispatch, getState) => {
    const ids = id ? Array.isArray(id) ? id : [id] : Object.keys(getState().elements);
    if (!ids.length) {
      dispatch({
        type: "@@element/SET_SELECTION_BOX_ACTIVE" /* EditorActionTypes.SET_SELECTION_BOX */,
        payload: {
          selectionBoxActive: false
        },
        undoable: false
      });
    }
    return dispatch({
      type: "@@element/selectable/SELECT" /* SelectableActionTypes.SELECT */,
      payload: {
        ids: ids,
        overwrite: overwrite
      },
      undoable: false
    });
  },
  deselect: id => (dispatch, getState) => {
    const ids = id ? Array.isArray(id) ? id : [id] : getState().selected;
    if (!ids.length) {
      return;
    }
    return dispatch({
      type: "@@element/selectable/DESELECT" /* SelectableActionTypes.DESELECT */,
      payload: {
        ids: ids
      },
      undoable: false
    });
  }
};
